import React from "react"
import Color from "color"

const MiniTextBlock = ({ currentCombination, borderWidth, boxPadding, ...props }) => {
  const contrast = Color(currentCombination.bg)
    .contrast(Color(currentCombination.color))
    .toFixed(2)

  return (
    <Div
      border={`${borderWidth}px solid`}
      p={3}
      bg='white'
      color='gray.0'
      textAlign="left"
      {...props}
    >
      <Flex>
        <Code style={{ textTransform: 'uppercase' }} fontSize={[2]}>
          {currentCombination.color} {currentCombination.bg}
        </Code>
        <P px={3} fontWeight={700} fontSize={5} bg={currentCombination.bg} color={currentCombination.color}>
          Aa 
        </P>
        <P px={3} fontWeight={700} fontSize={5} bg={currentCombination.color} color={currentCombination.bg}>
          Aa
        </P>
    <Div my={0} ml='auto'>
      <Span fontSize={2} fontWeight={600}>
        Contrast
      </Span>
      <Span fontSize={[5, 6,7]} fontWeight={800} display="block">
        {contrast}
      </Span>
    </Div>
    </Flex>
    </Div>
  )
}

export default MiniTextBlock
